import React,{useState} from 'react'
import {FaThumbsDown,FaThumbsUp} from 'react-icons/fa'
import './List.css'

const Ldl = () => {
    const[like,setLike]=useState(0);
    const[dislike,setDislike]=useState(0);
    return (
        <div>
            <button onClick={()=> {setLike(like+1)}} className='like'  ><FaThumbsUp/>{like}</button>
            <button onClick={()=> {setDislike(dislike+1)}} className='dislike' ><FaThumbsDown />{dislike}</button>
        </div>
    )
}

export default Ldl
