import React,{useEffect} from 'react'
import './Dashboard.css'
const  Alert = ({msg,type,removeAlert,list}) => {
  
    useEffect(() => {
       const timeout=setTimeout(() => {
           removeAlert();
       }, 2000);
       return ()=> clearTimeout(timeout);
    }, [list])



    return <p className={`alert alert-${type}`}>
          {msg} 
        </p>
    
}

export default Alert